const suffix = {
  "25": "25th percentile of national data",
  "25A": "25th percentile of national annual growth rates",
  "3": "3-month annualised growth rate",
  "6": "6-month annualised growth rate",
  "75": "75th percentile of national data",
  "75A": "75th percentile of national annual growth rates",
  A: "Annual growth rate",
  A10: "Annual growth rates calculated on averaged monthly index series",
  A20: "Annual growth rates calculated on averaged quarterly index series",
  B: "Average",
  E: "Euro",
  F: "Contribution to the M3 annual growth rate",
  F10:
    "Contribution to the M3 annual growth rate calculated on averaged monthly index series",
  I: "Interest rate",
  I10: "Share of cross border activity versus total activity",
  I11:
    "Share of cross border activity with residents in the UK versus the euro area and the UK total activity",
  I12:
    "Share of cross border activity with residents in the UK versus the euro area and the UK total cross-border activity",
  I20:
    "Indicator of concentration of the national cross-border shares, based on the Herfindhal index",
  I30: "Indicator of neutrality in the distribution of cross-border activity",
  I31:
    "Indicator of neutrality in the distribution of cross-border activity, for countries sharing a land border",
  I40:
    "Indicator of concordance in the cross-border activity, based on the Kendall co-graduation index",
  MA: "Maximum of national data",
  MAA: "Maximum of national annual growth rates",
  MI: "Minimum of national data",
  MIA: "Minimum of national annual growth rates",
  N: "National currency",
  P: "Growth rate to previous period",
  P10: "Currency ratio on total currency",
  PCT: "Percentage",
  V: "3-month moving average of annual growth rate",
  V00: "3-month moving average of index of notional stocks",
  V10: "3-month moving average of growth rate to previous period",
  V30: "3-month moving average of 3-month annualised growth rate",
  V60: "3-month moving average of 6-month annualised growth rate",
  W10:
    "National proportion of M3 notional stock over the aggregated euro area countries M3 notional stocks",
  WA: "Weighted average of national data",
  WAA: "Weighted average of national annual growth rates",
  Z: "Not applicable",
  L:'Level'
};

export const getDim12 = (element) => {
    let dim = "Z";
    let desc = suffix[dim]

    if(element===''||element===null){
      return [dim, desc];
    }
  
    try {
      dim = element;
      desc = suffix[element];
    } catch {}
    return [dim, desc];
  };
  