const createDateCols = (serie) => {
  // const obs = serie[0].Obs;

  // console.log(serie)
  let obs = [];
  let length = 0;
  if (serie[0]) {
    serie.forEach((oneSerie) => {
      if (oneSerie.Obs.length > length) {
        obs = oneSerie.Obs;
        length = oneSerie.Obs.length;
      }
    });
  } else {
    obs = serie.Obs;
  }

  let children = [];

  obs.forEach((ob) => {
    let date = getYYYYMM(ob.$.TIME_PERIOD);
    children.push({
      headerName: date,
      minWidth: 100,
      width: 140,
      valueFormatter: currencyFormat,
      field: date,
      type: ["rightAligned", "numericColumn"],
      enableValue: true,
      // enableRowGroup: true,
      enablePivot: true,
      // sortable:  true ,
    });
  });

  let dateGroup = {
    headerName: "Dates",
    groupId: "dates",
    children,
  };
  // console.log(dateGroup)
  return dateGroup;
};

