const createColDefs = (series) => {
  let columnDefs = [
    columnDefsSDMXHeader,
    columnDefsSDMXExplication,
    createDateCols(series),
  ];
  return columnDefs;
};

const createRowData = (series) => {
  let rowData = [];
  function row(se) {
    let res = "{";
    se.Obs.forEach((ob) => {
      let value = ":";
      if (!isNaN(ob.$.OBS_VALUE)) {
        value = ob.$.OBS_VALUE;
        // console.log(value)
        res = res + '"' + getYYYYMM(ob.$.TIME_PERIOD) + '":' + value + ",";
      } else {
        res = res + '"' + getYYYYMM(ob.$.TIME_PERIOD) + '":":",';
      }
    });
    res = res + '"end":0}';
    // console.log(res);
    // console.log(JSON.parse(res));
    // return JSON.parse(res);
    return res;
  }
  series.forEach((serie) => {
    const [sdmx, sdmxDesc] = getSDMXfromFED(serie);
    let res = {
      ...{
        sdmx: sdmx.join("."),
        dim1: sdmx[0] + " - " + sdmxDesc[0],
        dim2: sdmx[1] + " - " + sdmxDesc[1],
        dim3: sdmx[2] + " - " + sdmxDesc[2],
        dim4: sdmx[3] + " - " + sdmxDesc[3],
        dim5: sdmx[4] + " - " + sdmxDesc[4],
        dim6: sdmx[5] + " - " + sdmxDesc[5],
        dim7: sdmx[6] + " - " + sdmxDesc[6],
        dim8: sdmx[7] + " - " + sdmxDesc[7],
        dim9: sdmx[8] + " - " + sdmxDesc[8],
        dim10: sdmx[9] + " - " + sdmxDesc[9],
        dim11: sdmx[10] + " - " + sdmxDesc[10],
        dim12: sdmx[11] + " - " + sdmxDesc[11],
      },
      ...JSON.parse(row(serie)),
    };
    rowData.push(res);
  });
  return rowData;
};

const checkRowData = (data) => {
  let res = [];
  let length = 0;
  let rowData = [];
  data.forEach((oneData) => {
    if (Object.keys(oneData).length > length) {
      length = Object.keys(oneData).length;

      res = oneData;
    }
  });

  data.forEach((oneData) => {
    let row = "{";
    for (const [key] of Object.entries(res)) {
      let value = ":";
      if (
        oneData[key] === undefined ||
        oneData[key] === "" ||
        oneData[key] === null ||
        oneData[key].length < 0
      ) {
        value = ":";
        // row = row + '"' + key + '":":",';
      } else {
        value = oneData[key];
      }
      row = row + '"' + key + '":"' + value + '",';
    }
    row = row + '"end":0}';
    // console.log(row)
    rowData.push(JSON.parse(row));
  });
  return rowData;
};

const formatterSDW = (data) => {
  // console.log(data)
  data = data.MessageGroup;
  if (!data.DataSet.Series[0]) {
    data.DataSet.Series = [data.DataSet.Series];
    return data;
  } else {
    return data;
  }
};

export const getDataSDW = async (url) => {
  const [xml = null, date = ""] = await getXML(url);
  // console.log(xml);

  let sdwJs = await convertXml2js(xml);
  // console.log(sdwJs);

  // sdwJs=formatterSDW(sdwJs)
  // console.log(sdwJs)

  const messageGroup = formatterSDW(sdwJs);

  const name = messageGroup.Header.ID;

  const prepared = " (" + messageGroup.Header.Prepared + ")";

  const series = messageGroup.DataSet.Series;

  const columnDefs = createColDefs(series);
  // console.log(columnDefs);

  const rowData = createRowData(series, columnDefs);
  // const rowData = checkRowData(createRowData(series, columnDefs));
  // console.log(rowData);
  // checkRowData(rowData);

  return [name, prepared, date, columnDefs, rowData];
};
