import React from "react";
import { useState, useEffect } from "react";
import { getData, saveData, writeFS, readDataFS } from "../utils/source";
import ReactLoading from "react-loading";
import Grids from "../grid/Grids";

const Data = (props) => {
  const [loading, setLoading] = useState(null);
  const nom = props.nom;
  const dataName = props.dataName || "Aucune donnée, Veuillez actualiser";
  const url = props.url;
  const id = props.$;
  const date = props.date;
  const prepared = props.prepared || "";

  const handleRefresh = async () => {
    setLoading(true);

    let res = await getData(nom, url);
    // console.log(res)
    let [dataName, prepared, date, columnDefs, rowData] = res;
    let info = {
      date: date,
      dataName: dataName,
      prepared: prepared,
    };
    props.updateSource(id, columnDefs, rowData, info);
    props.updateBdfData();
    setLoading(false);
  };

  const handleOneDataClick = () => {
    props.lanceGrid({ $: id });
  };

  const [showGrid, setShowGrid] = useState();
  const [gridName, setGridName] = useState("");

  const handleOpenGrid = () => {
    setGridName(nom);
    setShowGrid(true);
  };

  const closeGrid = () => {
    setShowGrid(false);
    setGridName("");
  };

  if (showGrid) {
    return (
      <Grids
        closeGrid={closeGrid}
        columnDefs={props.columnDefs}
        rowData={props.rowData}
        activeGridName={gridName}
      />
    );
  } else {
    return (
      <React.Fragment>
        <li
          className="flex-even list-group-item bd-highlight p-2 "
          style={{ width: "" }}
        >
          <div className="d-flex flex-row justify-content-between align-items-stretch">
            <div className="">
              <div
                className=""
                // onClick={handleOneDataClick}
              >
                {dataName + " " + prepared}
              </div>
              {props.dataName && (
                <div className="  text-muted small">
                  Dernière mise à jour : {date}
                  {/* {!loading || <ReactLoading type={"bubbles"} color="black" />}  */}
                </div>
              )}
              <div>
                {!loading || (
                  <div
                    className="spinner-grow fast text-danger m-1"
                    role="status"
                  >
                    <span className="sr-only">Loading...</span>
                  </div>
                )}
              </div>
            </div>
            <div className="d-flex flex-row align-self-stretch mt-2">
              {Boolean(props.dataName) && (
                <div onClick={handleOneDataClick}>
                  <i className="p-1 fas fa-th green-text"></i>
                </div>
              )}

              <div>
                <a href={url} rel="noopener noreferrer" target="_blank">
                  <i className="p-1 fab fa-internet-explorer blue-text"></i>
                </a>
              </div>

              <div>
                {loading || (
                  <i
                    onClick={handleRefresh}
                    className="p-1 fas fa-redo red-text text-muted"
                  ></i>
                )}
                {!loading || (
                  <i
                    onClick={() => {
                      setLoading(false);
                    }}
                    className="p-1 fas fa-times red-text"
                  ></i>
                )}
              </div>
            </div>
          </div>
        </li>
      </React.Fragment>
    );
  }
};

export default Data;
