import React from "react";

import { Site } from "./Site";

export const Sites = (props) => {
  const source = props.source;
  const sites = [];

  source.forEach((oneSource) => {
    sites.push(
      <Site
        source={oneSource}
        updateSource={props.updateSource}
        key={oneSource.nom}
        lanceGrid={props.lanceGrid}
        updateBdfData={props.updateBdfData}

        // handleGrid={handleGridClick}
        // setColumnDefs={setColumnDefs}
        // setRowData={setRowData}
      />
    );
  });
  return <React.Fragment>{sites}</React.Fragment>;
};
