export const getDim1 = (element) => {
  let dim = "_";
  let desc = "_";
    switch (element) {
      case "FED":
        dim = "FED";
        desc = "Federal Reserve";
        break;
      case "BOE":
        dim = "BOE";
        desc = "Bank of England";
        break;
      case "SDW":
        dim = "SDW";
        desc = "Banque Centrale Européenne";
        break;
      case "INN":
        dim = "INN";
        desc = "l'INSEE";
        break;
      case "WEB":
        dim = "WEB";
        desc = "Webstat";
        break;
      case "EST":
        dim = "EST";
        desc = "Eurostat";
        break;
  
      default:
        dim = "___";
        desc = "_____________";
        break;
    }
    return [dim, desc];
  };
  