const originalMaturity = {
  A: "Total",
  AM:
    "Total calculated by weighting the volumes with a moving average (defined for cost of borrowing purposes)",
  B: "Short-Term",
  C: "Long-Term",
  D: "Up to 3 months",
  E: "Over 3 months",
  F: "Up to 1 year",
  // FM:
  //   "Up to 1 year calculated by weighting the volumes with a moving average (defined for cost of borrowing purposes)",
  G: "Over 1 and up to 2 years",
  H: "Over 2 years",
  HHL:
    "Original maturity over 2 years, remaining maturity over 2 years and interest rate reset within the next 24 months",
  HL: "Original maturity over 2 years and remaining maturity up to 2 years",
  I: "Over 1 and up to 5 years",
  J: "Over 5 years",
  K: "Over 1 year",
  KF: "Original maturity over 1 year and remaining maturity up to 1 year",
  KKF:
    "Original maturity over 1 year, remaining maturity over 1 year and interest rate reset within the next 12 months",
  KM:
    "Over 1 year calculated by weighting the volumes with a moving average (defined for cost of borrowing purposes)",
  L: "Up to two years",
  M: "Agreed maturity up to 2 years and redeemable at notice up to 3 months",
  N: "Agreed maturity over 2 years and redeemable at notice over 3 months",
  O: "Over 5 and up to 10 years",
  P: "Over 10 years",
  Q: "Over 3 months and up to 1 year",
  R: "Over 1 and up to 3 years",
  S: "Over 3 and up to 5 years",
  T: "Over 2 and up to 5 years",
  X: "Not applicable",
  Y:
    "Period of initial rate fixation below 1 year and original maturity over 1 year",
  Z: "Not allocated / Unspecified",
  // ZF: "Original maturity up to 1 year with unspecified period of fixation",
  // ZK: "Original maturity over 1 year with unspecified period of fixation",
};


export const getDim7 = (element) => {
    let dim = "_";
    let desc = "_";
  
    dim = element;
    try {
        desc = originalMaturity[element];
    } catch {}
    return [dim, desc];
  };