export const getDim6 = (element) => {
  let dim = "XXX";
  let desc = "Unspecified";
  // if (element === "" || element === null) {
  // }
  
  try {
    dim = element;
    desc = item[element];
    return [dim, desc];
  } catch {}
  return [dim, desc];
};
