const frequency = {
  A: "Annual",
  B: "Daily - businessweek",
  D: "Daily",
  E: "Event (not supported)",
  H: "Half-yearly",
  M: "Monthly",
  N: "Minutely",
  Q: "Quarterly",
  S:
    "Half-yearly, semester (value introduced in 2009 but H existed and is used in ESCB context)",
  W: "Weekly",
};
export const getDim2 = (element) => {
  let dim = "_";
  let desc = "_";

  if (element === 129 || element === "M") {
    dim = "M";
    desc = frequency["M"];
  } else {
    try {
      dim = element;
      desc = frequency[element];
    } catch {}
  }

  return [dim, desc];
};
