import React, { useState } from "react";
import InputFiles from "react-input-files";
import { MDBIcon } from "mdbreact";

export const UploadFile = (props) => {
  const [message, setMessage] = useState(null);

  const handleOnChange = (e) => {
    let files = e.target.files;
    let name = { ...e.target.files };
    if (name!==undefined&&name[0].name.toString().includes(".json")) {
      setMessage(name[0].name);
      let reader = new FileReader();
      reader.readAsText(files[0]);
      reader.onload = (e) => {
        let res = JSON.parse(e.target.result);
        if (res.source && res.bdf) {
          // console.log(res.bdf)
          // props.setSource(res.source);
          // props.setBdfData(res.bdf);
          props.openProject(res)
        } else {
          setMessage(
            "Mauvais fichier. Veuillez sélectionner un fichier Speed Web Extract (ex: SpeedWebExtract_semaine_date_mois_20xx_xx_xx.json)"
          );
        }
      };
    } else {
      setMessage(
        "Format de fichier non pris en charge. Veuillez sélectionner un fichier JSON (ex: SpeedWebExtract_semaine_date_mois_20xx_xx_xx.json)"
      );
    }
    // let files = e.target.files;
    // let name = { ...e.target.files };
    // setFileName(name[0].name);
    // let reader = new FileReader();
    // reader.readAsText(files[0]);
    // reader.onload = (e) => {
    //   let res = JSON.parse(e.target.result);

    // console.log(res);
    //   if (res.source && res.bdf) {
    // console.log("source");
    //     props.setSource(res.source);
    //     props.setBdfData(res.bdf);
    //   } else if (res.columnDefs && res.rowData && res.gridState) {
    //     props.restoreGridState(res);
    //   }
    // };
  };

  return (
    <React.Fragment>
      {/* <div class="form-group custom-file mb-3">
        <input
          type="file"
          class="custom-file-input"
          id="customFile"
          name="filename"
          onChange={handleOnChange}
        />
        <label class="custom-file-label" for="customFile">
          Open {children}
        </label>
      </div> */}
      {/* <InputFiles onChange={handleOnChange}>
      <div className='m-0'>

        <MDBIcon fas icon="folder-open green-text" />
          Open a Work Space
      </div>
 
      </InputFiles>
       */}
      <div className="d-flex flex-column">
        <label className="">
          {props.children}
          <input
            type="file"
            name="selectedFile"
            id="selectedFile"
            style={{ display: "none" }}
            onChange={handleOnChange}
          />{" "}
        </label>
        <div className="float-right">
          {message}{" "}
          {message && (
            <i
              href="/"
              class="fas fa-times red-text"
              onClick={() => {
                setMessage(null);
                // props.setSource([]);
                // props.setBdfData([]);
              }}
            ></i>
          )}
        </div>
      </div>
      {/* <input type="button" value="Browse..." onclick={document.getElementById('selectedFile').click()} >{message}</input> */}
    </React.Fragment>
  );
};

export default UploadFile;
