const Datas = (props) => {
  const [columnDefs, setColumnDefs] = useState([]);
  const [rowData, setRowData] = useState([]);
  // const datas = props.data;
  const datas = props.source.data;
  const link = [];
  // console.log(props)

  // useEffect(() => {
  //   console.log(rowData)

  //   console.log('useEffect')
  //   props.setColumnDefs(columnDefs)
  //   props.setRowData(rowData)
  // }, [rowData,columnDefs, props])

  for (const data in datas) {
    if (datas.hasOwnProperty(data)) {
      const element = datas[data];
      link.push(
        <Data
          $={element.$}
          dataName={element.nom}
          // donnees={element.donnees}
          url={element.url}
          date={element.date}
          nom={props.source.nom}
          key={element.$}
          columnDefs={element.donnees.columnDefs}
          rowData={element.donnees.rowData}
          // setColumnDefs={props.setColumnDefs}
          // setRowData={props.setRowData}
          // handleGrid={props.handleGrid}
          lanceGrid={props.lanceGrid}
          updateSource={props.updateSource}
          updateBdfData={props.updateBdfData}

        />
      );
    }
  }

  return (
    <React.Fragment>
      <ul className="list-group list-group-flush d-fle flex-ro bd-highlight mt-1">
        {link}
      </ul>
    </React.Fragment>
  );
};

export default Datas;
