const referenceSector = {
  A: "MFIs excluding ESCB",

  // A1:
  //   "MFIs excluding ESCB without being granted with a derogation under Article 9(1) of Regulation ECB/2013/33",

  // A2:
  //   "MFIs excluding ESCB granted with derogation under Article 9(1) of Regulation ECB/2013/33",

  B: "Credit and other institutions (MFI except MMFs and central banks)",

  C: "Eurosystem",

  E: "ECB",

  F:
    "Money Market Funds (MFIs excluding Eurosystem, credit institutions and other institutions)",

  G: "Central government and post office giro institutions",

  L: "Electronic money institutions",

  M: "MFIs excluding ECB",

  N: "NCBs",

  O: "MFIs excluding ESCB and credit institutions, issuing electronic money",

  P: "Non-MFIs issuing electronic money",

  R: "Credit institutions",

  S: "Payments institutions",

  S1: "Other payment service providers",

  S2: "Other payment service providers and e-money issuers",

  T: "Total, credit institutions and payment institutions",

  U: "Monetary Financial Institutions (MFIs)",

  V: "MFIs, central government and post office giro institutions",

  W: "Credit institutions legally incorporated in the reporting country",

  X: "Branches of euro area-based credit institutions",

  Y: "Branches of EEA-based credit institutions (outside the euro area)",

  Z: "Branches of non-EEA based banks",

  CB:"Commercial Banks"
};


export const getDim5 = (element) => {
    let dim = "_";
    let desc = "_";
  
    dim = element;
    try {
        desc = referenceSector[element];
    } catch {}
    return [dim, desc];
  };
  