const createDateCols = (cube) => {
  let children = [];

  // currencyFormat=''
  cube[0].Cube.forEach((cb) => {
    // if (cb.$.CAT_NAME==='OUTPUT IN'&&cb.$.CAT_VAL==='%') {
    //   children=rowPercentage(cb)
    // }
    // else if (cb.$.CAT_NAME==='OUTPUT IN'&&cb.$.CAT_VAL==='£') {
    //   children=rowCurrency(cb)
    // }

    if (cb.$.TIME) {
      let date = getYYYYMM(cb.$.TIME);

      children.push({
        headerName: date,
        minWidth: 100,
        width: 140,
        valueFormatter: currencyFormat,
        field: date,
        type: [
          "rightAligned",
          "numericColumn"
        ],
        enableValue: true,
        // enableRowGroup: true,
        enablePivot: true,
        // sortable:  true,
      });
    }
  });
  let dateGroup = {
    headerName: "Dates",
    groupId: "dates",
    children,
  };
  return dateGroup;
};

const createColDefs = (series) => {
  let columnDefs = [
    columnDefsSDMXHeader,
    columnDefsSDMXExplication,
    createDateCols(series),
  ];
  return columnDefs;
};

