const createRowData = (cube, colDefs) => {
  let rowData = [];
  function name(cube) {
    let res = "{";
    cube.forEach((cb) => {
      if (cb.$.TIME) {
        res = res + '"' + getYYYYMM(cb.$.TIME) + '":' + cb.$.OBS_VALUE + ',';
      }
    });
    res = res + '"end":0}';
    // console.log(JSON.parse(res))
    return res;
  }
  // console.log(Object.keys(cube).length)
  cube.forEach((oneCube) => {
    const [sdmx, sdmxDesc] = getSDMXfromBOE(oneCube);
    // console.log(oneCube)
    let res = {
      ...{
        sdmx: sdmx.join("."),
        dim1: sdmx[0] + " - " + sdmxDesc[0],
        dim2: sdmx[1] + " - " + sdmxDesc[1],
        dim3: sdmx[2] + " - " + sdmxDesc[2],
        dim4: sdmx[3] + " - " + sdmxDesc[3],
        dim5: sdmx[4] + " - " + sdmxDesc[4],
        dim6: sdmx[5] + " - " + sdmxDesc[5],
        dim7: sdmx[6] + " - " + sdmxDesc[6],
        dim8: sdmx[7] + " - " + sdmxDesc[7],
        dim9: sdmx[8] + " - " + sdmxDesc[8],
        dim10: sdmx[9] + " - " + sdmxDesc[9],
        dim11: sdmx[10] + " - " + sdmxDesc[10],
        dim12: sdmx[11] + " - " + sdmxDesc[11],
      },
      ...JSON.parse(name(oneCube.Cube)),
    };
    rowData.push(res);
  });
  // console.log(rowData)
  return rowData;
};

export const getDataBoE = async (url) => {
  // hi()
  // const res=

  // if(res==='error'){
  //   console.log(res)
  //   return res
  // }
  const [xml = null, date = ""] = await getXML(url);

  const boeJs = await convertXml2js(xml);
  console.log(boeJs);

  const cube = formatterBOE(boeJs).Envelope.Cube;

  const name = getDataName(cube);

  const columnDefs = createColDefs(cube);
  // console.log(columnDefs)

  const rowData = createRowData(cube, columnDefs);
  // console.log(rowData)

  return [name, "", date, columnDefs, rowData];
};
