// import "ag-grid-enterprise";
import { ColumnGroup, AutoWidthCalculator } from "ag-grid-community";
import { getData } from "../utils/source";
import DownloadFile from "../file/Download";
import { UploadFile } from "../file/Upload";
import { UploadGridData } from "../file/UploadGridData";
import { AgGridColumn } from "ag-grid-react/lib/agGridColumn";

// import all Enterprise modules
import { ModuleRegistry, AllModules } from "ag-grid-react";
import Select from "react-select";

//  @ag-grid-enterprise/all-modules';

// ModuleRegistry.registerModules(AllModules);

// // or only import minimal modules required for charts
// import { ModuleRegistry } from "@ag-grid-community/core";
// import { ClientSideRowModelModule } from "@ag-grid-community/client-side-row-model";
// import { ChartsModule } from "@ag-grid-enterprise/charts";

// ModuleRegistry.registerModules([ClientSideRowModelModule, ChartsModule]);

// import { AgGridReact } from '@ag-grid-community/react';
// import { AllModules } from '@ag-grid-enterprise/all-modules';
// import '@ag-grid-community/all-modules/dist/styles/ag-grid.css';
// import '@ag-grid-community/all-modules/dist/styles/ag-theme-alpine.css';

const Grids = (props) => {
  const [columnDefs, setColumnDefs] = useState();
  const [rowData, setRowData] = useState();
  const [gridApi, setgridApi] = useState();
  const [gridColumnApi, setGridColumnApi] = useState();
  const [gridState, setGridState] = useState();

  const defaultColDef = {
    sortable: true,
    filter: true,
    // rowDrag: true,
    resizable: true,
    autoHeight: true,
    // headerComponentFramework: SortableHeaderComponent,
    headerComponentParams: {
      menuIcon: "fa-bars",
    },
  };

  useEffect(() => {
    setColumnDefs(props.columnDefs);
    setRowData(props.rowData);
    // console.log(rowData);

    return () => {
      setColumnDefs([]);
      setRowData([]);
    };
  }, [props]);

  // useEffect(() => {
  //   setcolomnDefs(props.col);
  //   setrowData(props.row);
  //   // return () => {
  //   //     cleanup
  //   // }
  // }, [props]);

  // console.log(gridApi);
  const onButtonClick = () => {
    // const selectedNodes = gridApi.getSelectedNodes();
    // const selectedData = selectedNodes.map((node) => node.data);
    // const selectedDataString = selectedData
    //   .map((node) => node[0] + " " + node[1])
    //   .join(", ");

    // console.log(gridApi.getSelectedNodes().data);
    // console.log(selectedDataString);
    // const state={
    //   colState:gridColumnApi.getColumnState(),
    //   groupState:gridColumnApi.getColumnGroupState(),
    //   sortState:gridApi.getSortModel(),
    //   filterState:gridApi.getFilterModel()
    // }
    // setGridState(state)
    setGridState({
      colState: gridColumnApi.getColumnState(),
      groupState: gridColumnApi.getColumnGroupState(),
      sortState: gridApi.getSortModel(),
      filterState: gridApi.getFilterModel(),
    });
    // console.log(state)
    // console.log(gridApi);
    // console.log(gridColumnApi);
    // console.log(gridState);
  };

  const handleSaveBtnClick = () => {
    setGridState({
      colState: gridColumnApi.getColumnState(),
      groupState: gridColumnApi.getColumnGroupState(),
      sortState: gridApi.getSortModel(),
      filterState: gridApi.getFilterModel(),
    });
  };

  const handleFermerButtonClick = () => {
    props.closeGrid();
  };

  const onGridReady = (params) => {
    // gridApi = params.api;
    // console.log(params.api);
    setgridApi(params.api);
    setGridColumnApi(params.columnApi);
    // console.log(gridColumnApi)
  };

  useEffect(() => {
    if (gridState) {
      gridColumnApi.setColumnState(gridState.colState);
      gridColumnApi.setColumnGroupState(gridState.groupState);
      gridApi.setSortModel(gridState.sortState);
      gridApi.setFilterModel(gridState.filterState);
    }
  }, [gridState]);

  const restoreGridState = (res) => {
    setColumnDefs(res.columnDefs);
    setRowData(res.rowData);
    setGridState(res.gridState);
  };

  const [theme, setTheme] = useState("ag-theme-alpine");

  useEffect(() => {
    if (gridApi) {
      gridApi.resetRowHeights();
    }
  }, [theme]);

  const themeOption = [
    {
      value: "ag-theme-alpine",
      label: "Alpine",
    },
    {
      value: "ag-theme-alpine-dark",
      label: "Alpine dark",
    },
    {
      value: "ag-theme-balham",
      label: "Balham",
    },
    {
      value: "ag-theme-balham-dark",
      label: "Balham Dark",
    },
    {
      value: "ag-theme-blue",
      label: "Blue",
    },
    {
      value: "ag-theme-dark",
      label: "Sombre",
    },
    {
      value: "ag-theme-fresh",
      label: "Frais",
    },
    {
      value: "ag-theme-material",
      label: "Material",
    },
    {
      value: "",
      label: "None",
    },
  ];

  const handleThemeChange = (selectedOption) => {
    // gridApi.resetRowHeights();
    setTheme(selectedOption.value);
  };
  const filterOptions = (candidate, input) => {
    if (input) {
      return candidate.value === themeOption[0].value;
    }
    return true;
  };

  const inverseOrder = () => {
    if (columnDefs[0] && rowData[0]) {
      columnDefs[2].children.reverse();
      gridApi.setColumnDefs(columnDefs);
    }
  };

  const refreshGrid=()=>{
    if (columnDefs[0] && rowData[0]) {
      // scramble()
      gridColumnApi.resetColumnState()

    }
  }
  // console.log(rowData)
  return (
    <div className="d-flex flex-column">
      <div className="row">
        {/* <button type="button" onClick={onButtonClick}>
          Selected nodes
        </button> */}
        <div className="mt-1">
          <DownloadFile
            name={props.activeGridName}
            data={{ columnDefs, rowData, gridState }}
          >
            <button
              className="btn btn-outline-primary btn-sm"
              onClick={handleSaveBtnClick}
            >
              <i class="fas fa-download"></i> Enregistrer
            </button>
          </DownloadFile>
        </div>
        <div className="mt-1">
          <button
            className="btn btn-outline-danger btn-sm"
            type="button"
            onClick={handleFermerButtonClick}
          >
            <i class="fas fa-times"></i> Fermer
          </button>
        </div>

        <div className="mt-2 col-2 small">
          <Select
            options={themeOption}
            onChange={handleThemeChange}
            defaultValue={{
              value: "",
              label: "Choisissez un thème",
              isdisabled: true,
            }}
            isSearchable={false}
            className="basic-single "
            name="theme"
            classNamePrefix="select"
            // filterOption={filterOptions}
          />
        </div>
        <div className="col small mt-1">
          {Boolean(props.activeGridName === "new") && (
            <UploadGridData restoreGridState={restoreGridState} />
          )}
        </div>
        <div className="mt-1">
          <button
            className="btn btn-sm btn-outline-secondary rounded-pill"
            type="button"
            onClick={inverseOrder}
            data-toggle='tooltip'
            data-placement='bottom'
            title="Inverser l'ordre des colonnes"
          >
            <i class="fas fa-exchange-alt mx-n2"></i>
          </button>
        </div>
        <div className="mt-1">
          <button
            className="btn btn-sm btn-outline-light-green rounded-pill px-n2"
            type="button"
            onClick={refreshGrid}
            data-toggle='tooltip'
            data-placement='bottom'
            title="Réinitialiser toutes les colonnes"
          >
            <i class="fas fa-retweet mx-n2"></i>
          </button>
        </div>
      </div>
      <div className="">
        <div
          className={"shadow-lg border-0 rounded-lg " + theme}
          style={{
            width: "100%",
            height: "780px",
          }}
        >
          {/* <AgGridReact
          onGridReady={onGridReady}
          defaultColDef={defaultColDef}
          columnDefs={columnDefs}
          rowDragManaged={true}
          rowData={rowData}
          animateRows={true}
          rowSelection="multiple"
        /> */}
          <AgGridReact
            // modules={AllModules}
            immutableColumns={true}
            sideBar={{ toolPanels: ["columns", "filters"] }}
            defaultColDef={defaultColDef}
            rowDragManaged={true}
            enableRangeSelection={true}
            enableCharts={true}
            onGridReady={onGridReady}
            rowSelection="multiple"
            // sideBar={true}
            animateRows
            columnDefs={columnDefs}
            rowData={rowData}
            // modules={AllModules}
          />
        </div>
      </div>
    </div>
  );
};

Grids.propTypes = {};

export default Grids;
