function App() {
  const [source, setSource] = useState(dataSource);

  const updateSource = (id, col, row, info) => {
    if (source && id && col && row && info) {
      let element = source;
      setSource([]);
      element.forEach((oneElement) => {
        oneElement.data.forEach((oneData) => {
          if (oneData.$ === id) {
            oneData.donnees.columnDefs = col;
            oneData.donnees.rowData = row;
            oneData.date = info.date;
            oneData.prepared = info.prepared;
            oneData.nom = info.dataName.toString();
          }
        });
      });
      setSource([]);
      setSource(element);
      updateBdfData();
    } else {
      console.log("updateSource Erreur ....");
    }
  };

  const [bdfData, setBdfData] = useState(bdf);

  const updateBdfData = () => {
    // if (source) {
    let temp = bdfData;
    // setBdfData([]);
    let res = [];
    // console.log(source)
    source.forEach((oneSource) => {
      let col = [];
      let row = [];
      oneSource.data.forEach((oneData) => {
        // console.log( oneData.donnees.columnDefs)
        // if (
        //   oneData.donnees.columnDefs.length > 0 &&
        //   oneData.donnees.rowData.length > 0
        // ) {
        col = mergeColDefs(oneData.donnees.columnDefs, col);
        row = mergeRowDatas(oneData.donnees.rowData, row);
        // }
      });
      // console.log(col)
      if (col && row && col.length && row.length) {
        res.push({
          $: uuid(),
          nom: oneSource.nom,
          date: new Date(),
          donnees: {
            columnDefs: col,
            rowData: row,
          },
        });
      }
    });
    temp.data = res;
    // console.log(temp)
    setBdfData(temp);
    // } else {
    //   console.log("update Bdf Data erreur....");
    // }
  };

  useEffect(() => {
    console.log("bdfData update ....");
    updateBdfData();
  });

  const [columnDefs, setColumnDefs] = useState([]);
  const [rowData, setRowData] = useState([]);
  const [showGrid, setShowGrid] = useState(false);
  const [activeGridName, setActiveGridName] = useState("");

  const lanceGridBdf = (element) => {
    // console.log(element);
    if (element.$) {
      console.log(bdf);
      bdf.data.forEach((oneData) => {
        if (oneData.$ === element.$) {
          setColumnDefs(oneData.donnees.columnDefs);
          setRowData(oneData.donnees.rowData);
          setActiveGridName("All/" + oneData.nom);
        }
      });
      setShowGrid(true);
    } else if (element.nom && element.nom === "All") {
      let col = [];
      let row = [];
      // console.log(bdf.data)
      bdf.data.forEach((oneData) => {
        // console.log(col)
        col = mergeColDefs(oneData.donnees.columnDefs, col);
        row = mergeRowDatas(oneData.donnees.rowData, row);
      });
      // console.log(col)
      setColumnDefs(col);
      setRowData(row);
      setActiveGridName("All");

      setShowGrid(true);
    }
  };

  const lanceGrid = (element) => {
    if (element.$) {
      source.forEach((oneSource) => {
        oneSource.data.forEach((oneData) => {
          if (oneData.$ === element.$) {
            setColumnDefs(oneData.donnees.columnDefs);
            setRowData(oneData.donnees.rowData);
            setActiveGridName(oneSource.nom);
          }
        });
      });
      setShowGrid(true);
    } else if (element.nom) {
      source.forEach((oneSource) => {
        if (oneSource.nom === element.nom) {
          // console.log(oneSource);
          let col = [];
          let row = [];
          oneSource.data.forEach((oneData) => {
            // console.log(col)
            col = mergeColDefs(oneData.donnees.columnDefs, col);
            row = mergeRowDatas(oneData.donnees.rowData, row);
          });
          setColumnDefs(col);
          setRowData(row);
          setActiveGridName(oneSource.nom);
        }
      });
      setShowGrid(true);
    }
  };
  const closeGrid = () => {
    setColumnDefs([]);
    setRowData([]);
    setShowGrid(false);
    setActiveGridName();
  };

  const [isOpen, setIsOpen] = useState(false);

  const handleToggle = () => {
    setIsOpen(!isOpen);
  };

  const openProject = (res) => {
    setSource(res.source);
    setBdfData(res.bdf);
    updateBdfData();
  };
  const handleNewGridClick = () => {
    setColumnDefs([]);
    setRowData([]);
    setShowGrid(true);
    setActiveGridName("new");
  };

  const [showSourceOption, setShowSourceOption] = useState(false);

  const handleSourceClick = () => {
    setShowSourceOption(!showSourceOption);
  };

  const addNewUrl = (res) => {
    setSource(res);
  };

  const [showModal, setShowModal] = useState(false);
  const [mgs, setMgs] = useState("");

  return (
    // <ErrorHandler>
    <Router>
      <div className="App bg-drk">
        <Nav
          showGrid={showGrid}
          activeGridName={activeGridName}
          closeGrid={closeGrid}
          handleNewGridClick={handleNewGridClick}
          handleSourceClick={handleSourceClick}
          source={source}
          bdf={bdf}
          openProject={openProject}
        />

        {showSourceOption && (
          <AddNew
            source={source}
            handleSourceClick={handleSourceClick}
            addNewUrl={addNewUrl}
            setShowModal={setShowModal}
            setMgs={setMgs}
          />
        )}
        {showModal && (
          <Modal
            showModal={showModal}
            setShowModal={setShowModal}
            message={mgs}
          />
        )}

        <div className="d-flex flex-row ">
          <div className="container-fluid d-flex flex-column">
            <Main
              source={source}
              updateSource={updateSource}
              updateBdfData={updateBdfData}
              closeGrid={closeGrid}
              columnDefs={columnDefs}
              rowData={rowData}
              lanceGrid={lanceGrid}
              lanceGridBdf={lanceGridBdf}
              showGrid={showGrid}
              bdf={bdf}
              activeGridName={activeGridName}
            />
          </div>
        </div>

        {/* </Switch> */}
      </div>
    </Router>
    // </ErrorHandler>
  );
}

export default App;
