import React, { useState } from "react";

export const UploadGridData = (props) => {
  const [fileName, setFileName] = useState(null);

  const handleOnChange = (e) => {
    // console.log(e.target.files);
    let files = e.target.files;
    let name = { ...e.target.files };
    if (name&&name[0].name.toString().includes(".json")) {
      setFileName(name[0].name);
      let reader = new FileReader();
      reader.readAsText(files[0]);
      reader.onload = (e) => {
        // console.log(e.target.result);
        let res = JSON.parse(e.target.result);
        if (res.columnDefs && res.rowData && res.gridState) {
          props.restoreGridState(res);
        } else {
          setFileName(
            "Mauvais fichier. Veuillez sélectionner un fichier de données de grille (ex: gridData_semaine_date_mois_20xx_xx_xx.json)"
          );
        }
      };
    } else {
      setFileName(
        "Format de fichier non pris en charge. Veuillez sélectionner un fichier JSON (ex: gridData_semaine_date_mois_20xx_xx_xx.json)"
      );
    }
  };

//   console.log(props);
  return (
    <React.Fragment>
      <div class="form-grou mt-2 pt-1 custom-file mb-3">
        <input
          type="file"
          class="custom-file-input"
          id="customFile"
          name="filename"
          onChange={handleOnChange}
        />
        <label class="custom-file-label" for="customFile">
          {fileName ||
            "Choisir un fichier Grid Data (ex: gridData_semaine_date_mois_20xx_xx_xx.json)"}
        </label>
      </div>
    </React.Fragment>
  );
};

export default UploadGridData;
