import {
    getXML,
    convertXml2js,
    currencyFormat,
    columnDefsSDMXHeader,
    columnDefsSDMXExplication
} from '../utils';
import { getYYYYMM } from "../date";
import { getDim1 } from '../dim/dim1';
import { getDim2 } from '../dim/dim2';
import { getDim4 } from '../dim/dim4';
import { getDim5 } from "../dim/dim5";
import { getDim7 } from "../dim/dim7";
import { getDim8 } from "../dim/dim8";
import { getDim3 } from "../dim/dim3";
import { getDim11 } from "../dim/dim11";
import { getDim6 } from '../dim/dim6';
import { getDim10 } from '../dim/dim10';

const createDateCols = (serie) => {
  const obs = serie[0].Obs;

  let children = [];

  obs.forEach((ob) => {
    let date = getYYYYMM(ob.$.TIME_PERIOD);
    children.push({
      headerName: date,
      minWidth: 100,
      width: 140,
      valueFormatter: currencyFormat,
      field: date,
      // type: "dates",
      enableValue: true,
      // type: 'rightAligned',
      type: ["rightAligned", "numericColumn"],

      // enableRowGroup: true,
      enablePivot: true,
      // sortable:  true ,
    });
  });

  let dateGroup = {
    headerName: "Dates",
    groupId: "dates",
    children,
  };
  return dateGroup;
};

const getSDMXfromFED = (serie) => {
  let dim1 = ""; //FED
  let dim2 = "";
  let dim3 = "";
  let dim4 = "";
  let dim5 = "";
  let dim6 = "";
  let dim7 = "";
  let dim8 = "";
  let dim9 = "";
  let dim10 = "";
  let dim11 = "";
  let dim12 = "";

  let desc1 = "";
  let desc2 = "";
  let desc3 = "";
  let desc4 = "";
  let desc5 = "";
  let desc6 = "";
  let desc7 = "";
  let desc8 = "";
  let desc9 = "";
  let desc10 = "";
  let desc11 = "";
  let desc12 = "";

  let $ = serie.$;

  //dim1
  // dim1 = "FED";
  // desc1 = "Federal Reserve";
  [dim1, desc1] = getDim1("FED");

  //dim2
  // if ($.FREQ === 129) {
  //   dim2 = "M";
  //   desc2 = "Monthly";
  // }
  [dim2, desc2] = getDim2($.FREQ);

  //dim4
  // if ($.SA === "SA") {
  //   dim4 = "A";
  //   desc4 = "Seasonally Adjusted";
  // } else if ($.SA === "NSA") {
  //   dim4 = "N";
  //   desc4 = "Non-Seasonally Adjusted";
  // }


  [dim4, desc4] = getDim4($.SA);

  //dim5
  [dim5,desc5]=getDim5($.BG)
  // if ($.BG === "CB") {
  //   dim5 = "A";
  //   desc5 = "Commercial Banks";
  // }

  //dim6
  //dim10
  switch ($.ITEM) {
    case 1002:
      dim6 = "A30";
      desc6 = "Securities in bank credit";
      dim10 = "0000";
      desc10 = "All";
      break;
    case 1023:
      dim6 = "CIL";
      desc6 = "Commercial and industrial loans";
      dim10 = "2240";
      desc10 = "NFCs";
      break;
    case 1026:
      dim6 = "A22";
      desc6 = "Real estates loans";
      dim10 = "0000";
      desc10 = "All";
      break;
    case 1221:
      dim6 = "A22";
      desc6 = "Residential real estate loans";
      dim10 = "2250";
      desc10 = "Households";
      break;
    case 3219:
      dim6 = "CRE";
      desc6 = "Commercial real estate loans";
      dim10 = "2240";
      desc10 = "NFCs";
      break;
    default:
      [dim6,desc6] = getDim6('');
      [dim10,desc10] = getDim10($.ITEM);
      break;
  }

  //dim12
  switch ($.H8_UNITS) {
    case "LEVEL":
      dim12 = "E";
      desc12 = "Level";
      break;
    case "RATE":
      dim12 = "A";
      desc12 = "Growth rate";
      break;
    default:
      dim12 = "_";
      desc12 = "_____";
      break;
  }

  //dim3
  // dim3 = "US";
  // desc3 = "USA";
  [dim3, desc3] = getDim3("US");


  //dim7
  // dim7 = "A";
  // desc7 = "Total";
  [dim7,desc7]=getDim7($.CATEGORY);

  //dim8
  // dim8 = "1";
  // desc8 = "Outstanding amounts";
  [dim8,desc8]=getDim8('1')


  //dim9
  dim9 = "U6";
  desc9 = "Résident";

  //dim11
  // dim11 = "USD";
  // desc11 = "USD";
  [dim11,desc11]=getDim11('USD')


  const sdmx = [
    dim1,
    dim2,
    dim3,
    dim4,
    dim5,
    dim6,
    dim7,
    dim8,
    dim9,
    dim10,
    dim11,
    dim12,
  ];
  const sdmxDesc = [
    desc1,
    desc2,
    desc3,
    desc4,
    desc5,
    desc6,
    desc7,
    desc8,
    desc9,
    desc10,
    desc11,
    desc12,
  ];
  return [sdmx, sdmxDesc];
};

const createColDefs = (series) => {
  let columnDefs = [
    columnDefsSDMXHeader,
    columnDefsSDMXExplication,
    createDateCols(series),
  ];
  return columnDefs;
};

const createRowData = (series, colDefs) => {
  let rowData = [];
  function row(se) {
    let res = "{";
    se.Obs.forEach((ob) => {
      res =
        res + '"' + getYYYYMM(ob.$.TIME_PERIOD) + '":' + ob.$.OBS_VALUE + ",";
    });
    res = res + '"end":0}';
    return res;
  }
  series.forEach((serie) => {
    const [sdmx, sdmxDesc] = getSDMXfromFED(serie);
    let res = {
      ...{
        sdmx: sdmx.join("."),
        dim1: sdmx[0] + " - " + sdmxDesc[0],
        dim2: sdmx[1] + " - " + sdmxDesc[1],
        dim3: sdmx[2] + " - " + sdmxDesc[2],
        dim4: sdmx[3] + " - " + sdmxDesc[3],
        dim5: sdmx[4] + " - " + sdmxDesc[4],
        dim6: sdmx[5] + " - " + sdmxDesc[5],
        dim7: sdmx[6] + " - " + sdmxDesc[6],
        dim8: sdmx[7] + " - " + sdmxDesc[7],
        dim9: sdmx[8] + " - " + sdmxDesc[8],
        dim10: sdmx[9] + " - " + sdmxDesc[9],
        dim11: sdmx[10] + " - " + sdmxDesc[10],
        dim12: sdmx[11] + " - " + sdmxDesc[11],
      },
      ...JSON.parse(row(serie)),
    };
    rowData.push(res);
  });
  return rowData;
};

export const getDataFED = async (url) => {
  const [xml = null, date = ""] = await getXML(url);

  const fedJs = await convertXml2js(xml);
  // console.log(fedJs)

  const messageGroup = fedJs.MessageGroup;

  const name = messageGroup.Header.Name;

  const prepared = " (" + messageGroup.Header.Prepared + ")";

  const series = messageGroup.DataSet.Series;

  const columnDefs = createColDefs(series);

  const rowData = createRowData(series, columnDefs);

  return [name, prepared, date, columnDefs, rowData];
};
