import React, { useEffect, useState } from "react";
import {
  MDBIcon,
  MDBSideNavCat,
  MDBSideNavNav,
  MDBSideNav,
  MDBSideNavLink,
  MDBContainer,
  MDBRow,
  MDBBtn,
  MDBNavbar,
  MDBNavbarBrand,
  MDBNavbarToggler,
  MDBCollapse,
  MDBNavbarNav,
  MDBNavItem,
  MDBNavLink,
  MDBDropdown,
  MDBDropdownToggle,
  MDBDropdownMenu,
  MDBDropdownItem,
} from "mdbreact";
import { UploadFile } from "../file/Upload";
import { DownloadFile } from "../file/Download";

export const Nav = (props) => {
  // console.log(__dirname + "/public/img/Logo_SWE.png");
  const source = props.source;
  const bdf = props.bdf;
  const [isOpen, setIsOpen] = useState(false);
  const handleToggle = () => {
    setIsOpen(!isOpen);
  };

  const columnDefs=props.columnDefs
  const rowData=props.rowData
  const gridState=props.gridState

  //   const handleNewGridClick = () => {
  //     props.setColumnDefs([]);
  //     props.setRowData([]);
  //     props.setShowGrid(true);
  //     props.setActiveGridName("new");
  //   };
  return (
    <React.Fragment>
      <div
        onMouseLeave={() => {
          setIsOpen(false);
        }}
        onMouseEnter={() => {
          setIsOpen(true);
        }}
      >
        <MDBNavbar
          className="navbar navbar-light bg-light"
          style={{ fontFamily: "Monda", fontSize: "18px" }}
          scrolling
          // fixed="top"
        >
          <MDBNavbarBrand href="#">
            <div
              className="navbar-brand text-monosace font-weight-bold"
              // style={{ fontFamily: "Indie Flower" }}
            >
              {/* <img
                src={__dirname + "/public/img/Logo_SWE.png"}
                height="30"
                alt="SWE"
                loading="lazy"
              /> */}
              <div className="pt-3">
                <img
                  src={require("../sites/img/Logo_SWE.png")}
                  className="rounded float-left my-n5"
                  height="90px"
                  width="90px"
                  alt="..."
                />
              </div>
              {/* <img
                className=""
                width="250px"
                height="250px"
                src={require(__dirname+'/src/components/sites/img/Logo_SWE.png')}
                alt="img"
              /> */}
              {"   "}
              {/* Speed Web Extract */}
            </div>
          </MDBNavbarBrand>
          {props.showGrid && <div>{props.activeGridName}</div>}

          <MDBNavbarToggler onClick={handleToggle} />
          <MDBCollapse isOpen={isOpen} navbar>
            <MDBNavbarNav left>
              <MDBNavItem>
                <a
                  href="#"
                  className="nav-link border-0 mt-4"
                  onClick={props.closeGrid}
                >
                  <i className="fas fa-home amber-text p-0 m-0 ml-1"></i> Home
                </a>
              </MDBNavItem>
              {props.showGrid || (
                <MDBNavItem>
                  <div className="nav-link border-0 " href="#">
                    <UploadFile openProject={props.openProject}>
                      <MDBIcon icon="fas fa-folder-open green-text ml-1" /> Ouvrir
                    </UploadFile>
                  </div>
                </MDBNavItem>
              )}
              <MDBNavItem>
                <a
                  href="#"
                  className="nav-link border-0 mt-n1"
                  onClick={props.handleNewGridClick}
                >
                  <i className="fas fa-th indigo-text "></i> Créer une table
                </a>
              </MDBNavItem>
              {props.showGrid || (
                <MDBNavItem>
                  <a
                    href="#"
                    className="nav-link border-0 "
                    onClick={props.handleSourceClick}
                  >
                    <i className="fas fa-database orange-text"></i> Ajouter une
                    Source
                  </a>
                </MDBNavItem>
              )}
              {props.showGrid || (
                <MDBNavItem>
                  <DownloadFile name={"SpeedWebExtract"} data={{ source, bdf }}>
                    <div className="nav-item p-1 py- my-2 ml-0">
                      <MDBIcon icon="file-download pr-2 red-text" size="1x" />
                      Télécharger la sélection
                    </div>
                  </DownloadFile>
                </MDBNavItem>
              )}
             
            </MDBNavbarNav>
          </MDBCollapse>
        </MDBNavbar>
      </div>
    </React.Fragment>
  );
};
