import React, { useState, useEffect } from "react";
import { Site } from "../sites/Site";
// import { Container, Row, Col } from "reactstrap";
import { BrowserRouter as Router, Switch, Route, Link } from "react-router-dom";
// import Image from "react-bootstrap/Image";
import Grids from "../grid/Grids";
import Bdf from "../bdf/Bdf";
import {
  MDBContainer,
  MDBBtn,
  MDBModal,
  MDBModalBody,
  MDBModalHeader,
  MDBModalFooter,
} from "mdbreact";
import { Sites } from "../sites/Sites";

export const Main = (props) => {

  const [showModal, setShowModal] = useState(true);
  const [afficheGrid, setAfficheGrid] = useState(null);

  useEffect(() => {
    if (props.showGrid) {
      if (props.columnDefs.length <= 0) {
        if (props.activeGridName === "new") {
          setShowModal(false);
          setAfficheGrid(true);
        } else {
          setShowModal(true);
          setAfficheGrid(false);
        }
      } else {
        setShowModal(false);
        setAfficheGrid(true);
      }
    } else {
      setAfficheGrid(false);
      setShowModal(false);
    }
    return () => {
      setAfficheGrid(null);
      setShowModal(null);
    };
  }, [props.showGrid, props.columnDefs.length]);

  const toggle = () => {
    props.closeGrid();
    setAfficheGrid(null);
    setShowModal(null);
  };

  if (afficheGrid) {
    return (
      <Grids
        closeGrid={props.closeGrid}
        columnDefs={props.columnDefs}
        rowData={props.rowData}
        activeGridName={props.activeGridName}
        gridState={props.gridState}
        setGridState={props.setGridState}
      />
    );
  } else if (!afficheGrid) {
    return (
      <React.Fragment>
        <div>
          {showModal && (
            <MDBContainer>
              <MDBModal
                isOpen={showModal}
                toggle={toggle}
                side
                position="top-right"
              >
                {/* <MDBModalHeader toggle={toggle}>MDBModal title</MDBModalHeader> */}
                <MDBModalBody>
                  {/* <i class="fas fa-exclamation-triangle red-text"></i> */}
                  <i className="fas fa-exclamation-circle red-text"></i> Aucune
                  donnée trouvée, Veuillez réessayer...
                  <i
                    className="fas fa-times grey-text mt-1 float-right"
                    onClick={toggle}
                  ></i>
                </MDBModalBody>
              </MDBModal>
            </MDBContainer>
          )}

          <div className="m-3">
            <div className="col">
              {/* {sites} */}
              <Sites
                source={props.source}
                updateSource={props.updateSource}
                lanceGrid={props.lanceGrid}
                updateBdfData={props.updateBdfData}
              />
              <Bdf
                source={props.source}
                bdfData={props.bdf}
                lanceGridBdf={props.lanceGridBdf}
              />
            </div>
          </div>
        </div>
      </React.Fragment>
    );
  }
};
