import React, { useState, useEffect } from "react";
import Select from "react-select";
import {
  MDBContainer,
  MDBBtn,
  MDBModal,
  MDBModalBody,
  MDBModalHeader,
  MDBModalFooter,
  MDBRow,
  MDBCol,
  MDBInput,
} from "mdbreact";
import { v4 as uuid } from "uuid";
import { getData } from "../utils/source";
import { Modal } from "../notification/Modal";
import ErrorHandler from "../error/ErrorHandler";

export const AddNew = (props) => {
  const changeHandler = (event) => {
    // setState({ [event.target.name]: event.target.value });
    let res = [event.target];
    console.log(event.target.value);
    console.log(event.target.name);
    // state[res] = event.target.value;
  };
  const [source, setSource] = useState(props.source);

  const option = [];

  source.forEach((oneSource) => {
    let nom = oneSource.nom;
    let img = oneSource.img;

    option.push({
      key: { nom },
      value: { nom },
      label: (
        <div className="d-flex flex-row">
          <div className="mt-2 col-10">{nom}</div>
          <div className="col-2">
            <img
              className="card-img-right flex-auto d-none d-md-block"
              width="40px"
              height="40px"
              src={require("../sites/img/" + img)}
              alt="img"
            />
          </div>
        </div>
      ),
    });
  });

  const [nom, setNom] = useState("");
  const [date, setDate] = useState("xx/xx/xxxx");
  const [dataNom, setDataNom] = useState("");
  const [url, setUrl] = useState("");
  const [prepared, setPrepared] = useState("");
  const [columnDefs, setColumnDefs] = useState([]);
  const [rowData, setRowData] = useState([]);
  const [isLoading, setIsLoading] = useState(null);

  // const handleRefresh = async () => {
  //   setLoading(true);

  //   let res = await getData(nom, url);
  //   let [dataName, prepared, date, columnDefs, rowData] = res;
  //   let info = {
  //     date: date,
  //     dataName: dataName,
  //     prepared: prepared,
  //   };
  //   setLoading(false);
  //   props.updateSource(id, columnDefs, rowData, info);
  //   props.updateBdfData();
  // };

  const [isUrlOk, setIsUrlOk] = useState(null);
  const handleCheckURL = async () => {
    setIsLoading(true);

    if (nom && nom.length > 0 && url.includes(".") && url.length > 20) {
      let res = await getData(nom, url);
      if (res[0]) {
        let [dataName, prepared, date, columnDefs, rowData] = res;
        // console.log("object");
        if (
          columnDefs &&
          rowData &&
          columnDefs.length > 0 &&
          rowData.length > 0
        ) {
          setDataNom(dataName);
          setPrepared(prepared);
          setDate(date);
          setColumnDefs(columnDefs);
          setRowData(rowData);
          // console.log(columnDefs);
          // console.log(rowData);
          setIsUrlOk(true);
        } else {
          setIsUrlOk(false);
          console.log(columnDefs);
        }
      } else {
        console.log("no");
        setIsUrlOk(false);
        // setLoading(false);
      }
    } else {
      setIsUrlOk(false);
    }

    // setTimeout(() => {
    //   console.log('Hello, World!')
    //   setIsLoading(false);
    //   console.log('isloadin 2 '+isLoading)
    // }, 3000);
    // console.log("fuck");
    // console.log(isUrlOk);
    // setIsUrlOk(false)
    setIsLoading(false);

    // props.updateSource(id, columnDefs, rowData, info);
    // props.updateBdfData();
  };
  let buttonContent = "Check URL...";

  if (isLoading) {
    buttonContent = (
      <div>
        Checking URL
        <div className="spinner-grow text-blue  float-right  m-n2 " role="status">
          <span className="sr-only float-right">Loading...</span>
        </div>
      </div>
    );
  } else {
    if (isUrlOk) {
      buttonContent = (
        <div>
          URL is OK
          <i className="fas fa-check fa-2x float-right m-n1 green-text"></i>
        </div>
      );
    } else if (isUrlOk === false) {
      buttonContent = (
        <div>
          URL is bad!
          <i className="fas fa-times fa-2x float-right m-n1 red-text"></i>
        </div>
      );
    } else {
      buttonContent = "Check URL.";
    }
  }

  const submitHandler = (event) => {
    event.preventDefault();
    event.target.className += " was-validated";
  };

  const handleSaveBtnClick = () => {
    let tempSource = source;
    if (isUrlOk && nom && url && date && prepared && columnDefs && rowData) {
      tempSource.forEach((oneSource) => {
        if (oneSource.nom === nom) {
          oneSource.data.push({
            $: uuid(url),
            nom: dataNom,
            prepared: prepared,
            url: url,
            date: date,
            donnees: {
              columnDefs: columnDefs,
              rowData: rowData,
            },
          });
        }
      });
      props.addNewUrl(tempSource);
      props.setMgs(" La source (" + nom + ") a été ajoutée avec succès");
      props.setShowModal(true);
      props.handleSourceClick(false);
    } else {
      // console.log(tempSource);
      props.setMgs(" Aucune source n'a été ajoutée..");
      props.setShowModal(true);
      props.handleSourceClick(false);
    }
    console.log('something wrong')
  };
  

  return (
    // <ErrorHandler>

    <div>
      <MDBContainer>
        <MDBModal isOpen={true} toggle={props.handleSourceClick}>
          <MDBModalHeader toggle={props.handleSourceClick}>
            Ajouter une source
          </MDBModalHeader>
          <MDBModalBody>
            <div>
              <form
                className="needs-validation"
                onSubmit={submitHandler}
                noValidate
              >
                <MDBRow>
                  <MDBCol md="12">
                    <label>Sélectionnez une source</label>
                    <Select
                      options={option}
                      onChange={(selectedOption) => {
                        // console.log(selectedOption.value.nom);
                        setNom(selectedOption.value.nom);
                      }}
                      isSearchable={false}
                      className="basic-single browser-default custom-"
                      name="nom"
                      // defaultValue={"colourOptions[0]"}
                      // value={nom}
                    />
                    {/* <select
                      onChange={(e) => {
                        setNom(e.target.value);
                      }}
                      required
                      className="browser-default custom-select btn-group"
                      role="group"
                      name="nom"
                      value={nom}
                    >
                      <option className=" p-2"></option>
                    </select> */}
                    <div className="invalid-feedback">
                      Veuillez choisir un source
                    </div>
                  </MDBCol>
                  <MDBCol md="12">
                    <MDBInput
                      value={url}
                      name="lname"
                      onChange={(e) => {
                        setUrl(e.target.value);
                      }}
                      type="text"
                      label="URL"
                      required
                    >
                      {nom && (
                        <small id="emailHelp" className="form-text text-muted">
                          Entrez une URL valide du site : {nom}
                          {/* Saisir une URL de téléchargement de données {nom}  */}
                        </small>
                      )}
                      <div className="valid-feedback">Looks good!</div>
                      <div className="invalid-feedback">
                        l'URL que vous avez saisie n'est pas correcte, Veuillez
                        réessayer
                      </div>{" "}
                    </MDBInput>
                    <button
                      type="submit"
                      className="btn btn-outline-default btn-block"
                      disabled={false}
                      onClick={handleCheckURL}
                    >
                      {/* {(isLoading || isUrlOk) || "Check URL"} */}
                      {buttonContent}
                      {/* {!isLoading || (
                        <div>
                          Checking URL
                          <div
                            class="spinner-grow text-blue  float-right  m-n2 "
                            role="status"
                          >
                            <span class="sr-only float-right">Loading...</span>
                          </div>
                        </div>
                      )}
                      {isLoading ||
                        (isUrlOk === true && (
                          <div>
                            URL is bad!
                            <i class="fas fa-times fa-2x float-right m-n1 red-text"></i>
                          </div>
                        ))}
                      {isLoading ||
                        (isUrlOk === false && (
                          <div>
                            URL is OK
                            <i class="fas fa-check fa-2x float-right m-n1 green-text"></i>
                          </div>
                        ))} */}
                    </button>
                  </MDBCol>
                </MDBRow>
              </form>
            </div>
          </MDBModalBody>
          <MDBModalFooter>
            <MDBBtn color="secondary" onClick={props.handleSourceClick}>
              Fermer
            </MDBBtn>
            <MDBBtn color="primary" onClick={handleSaveBtnClick}>
              Enregistrer la source
            </MDBBtn>
          </MDBModalFooter>
        </MDBModal>
      </MDBContainer>
    </div>
    // </ErrorHandler>

  );
};
