export const readData = (fileName) => {
  const file = "../../data/" + fileName + ".json";
  try {
    return jsonfile.readFileSync(file);
  } catch (e) {
    console.error(e);
    return null;
  }
};

export const saveData = (data, fileName) => {
  let file = "../../data/" + fileName + ".json";
  try {
    jsonfile.writeFileSync(file, data);
    return true;
  } catch (e) {
    console.error(e);
    return false;
  }
};

