export const readDataFS = (fileName) => {
  const file = "/home/" + fileName + ".json";
  let data = "{'e':'rrrr'}";
  writeFS(data, file);
  try {
    return jsonfile.readFileSync(file);
  } catch (e) {
    console.error(e);
    return null;
  }
};

export const mergeRowDatas = (row1, row2) => {
  // console.log(row1)
  // console.log(row2)
  let res = [];
  row1.forEach((row) => {
    // console.log(row)
    res.push(row);
  });
  row2.forEach((row) => {
    // console.log(row)
    res.push(row);
  });
  // console.log(res)
  return res;
};

const getStartEndDates = (arr) => {
  if (!arr[0]) {
    return [0, 0];
  }
  return [
    arr[2].children[0].field,
    arr[2].children[arr[2].children.length - 1].field,
  ];
};

export const mergeColDefs = (col1, col2) => {
  // console.log(col1)
  // console.log(col2)
  if (col1.length === 0) {
    return col2;
  } else if (col2.length === 0) {
    return col1;
  } else {
    const [startCol1, endCol1] = getStartEndDates(col1);
    const [startCol2, endCol2] = getStartEndDates(col2);

    // console.log(startCol1)
    // console.log(startCol2)
    // console.log(endCol1)
    // console.log(endCol2)

    if (startCol1 === startCol2 && endCol1 === endCol2) {
      if (col2[2].children.length === col1[2].children.length) {
        return col1;
      }
    } else {
      let start = "";
      if (startCol1 < startCol2) {
        start = startCol1;
      } else if (startCol1 >= startCol2) {
        start = startCol2;
      }
      let end = "";
      // console.log(endCol1)
      // console.log(endCol2)
      if (endCol1 < endCol2) {
        end = endCol2;
      } else if (endCol1 >= endCol2) {
        end = endCol1;
      }

      let dateRange = createDateRange(start + "-01", end + "-01");
      // console.log(dateRange);
      let children = [];

      dateRange.forEach((date) => {
        children.push({
          headerName: date,
          minWidth: 100,
          width: 140,
          valueFormatter: currencyFormat,
          field: date,
          type: ["rightAligned", "numericColumn"],
          enableValue: true,
          // enableRowGroup: true,
          enablePivot: true,
          // sortable:  true,
        });
      });
      let dateGroup = {
        headerName: "Dates",
        groupId: "dates",
        children,
      };
      col1[2] = dateGroup;
      // console.log(col1[2])
      // console.log(col1)
      return col1;
    }
  }
  // console.log(!col1)
  // console.log(col1.length)
};

