import React, { useEffect, useState } from "react";
import BdfOneSource from "./BdfOneSource";

export const BdfSources = (props) => {
  // console.log(props);
  const source = props.source;

  let bdfSource = [];
  // console.log(props.source.data);
  source.data.forEach((oneData) => {
    // if (
    //   oneData.donnees.columnDefs.length > 0 &&
    //   oneData.donnees.rowData.length > 0
    // ) {
      bdfSource.push(
        <BdfOneSource
          key={oneData.$}
          id={oneData.$}
          nom={oneData.nom}
          date={oneData.date}
          oneSource={oneData}
          lanceGridBdf={props.lanceGridBdf}
        />
      );
    // }

    // console.log(bdfSource);
  });
  return (
    <React.Fragment>
      <ul className="list-group list-group-flush d-fle flex-ro bd-highlight mt-1">
        {bdfSource}
        {/* {source.data.forEach((oneData) => 
          <BdfOneSource
            key={oneData.$}
            id={oneData.$}
            nom={oneData.nom}
            date={oneData.date}
            oneSource={oneData}
          />
        )} */}
      </ul>
    </React.Fragment>
  );
};

