import React, { useState } from "react";
import {
  MDBContainer,
  MDBBtn,
  MDBModal,
  MDBModalBody,
  MDBModalHeader,
  MDBModalFooter,
} from "mdbreact";

export const Modal = (props) => {
  const [isOpen, setIsOpen] = useState(props.showModal);
  const toggle = () => {
    setIsOpen(!isOpen);
    props.setShowModal(!props.showModal);
  };
  return (
    <MDBContainer>
      <MDBModal isOpen={isOpen} toggle={toggle} side position="top-right">
        {/* <MDBModalHeader toggle={toggle}>MDBModal title</MDBModalHeader> */}
        <MDBModalBody>
          {/* <i class="fas fa-exclamation-triangle red-text"></i> */}
          <i className="fas fa-exclamation-circle "></i>
          {props.message}
          <i
            className="fas fa-times grey-text mt-1 float-right"
            onClick={toggle}
          ></i>
        </MDBModalBody>
      </MDBModal>
    </MDBContainer>
  );
};
