import React from "react";
import { render } from "react-dom";
import Download from "@axetroy/react-download";

export const DownloadFile = ({ name, data, children }) => {
  // const {myData} = this.state; // I am assuming that "this.state.myData"
  //                              // is an object and I wrote it to file as
  //                              // json
  const options = {
    weekday: "long",
    year: "numeric",
    month: "long",
    day: "numeric",
  };
  const fileName =
    name +
    "_" +
    new Date().toLocaleDateString("fr-FR", options) +
    "_" +
    new Date().toLocaleTimeString();

  let json = JSON.stringify(data);
  const blob = new Blob([json], { type: "application/json" });
  const href = URL.createObjectURL(blob);

  const download = fileName + ".json";

  // console.log(data);
  return (
    <a href={href} download={download} className="black-text">
      {children}
    </a>
    /* <Download file={download} content={blob}>
      <button type="button">{children}</button>
    </Download> */
  );
};

export default DownloadFile;
